.quotes {
   padding: 6.81rem 0;

   background: linear-gradient(180deg, #EAF0FF 0%, #F6F9FF 100%);

   @media screen and(max-width:640px) {
      padding: 1.81rem 0;
   }

   &__inner {
      display: flex;
      justify-content: space-between;

      @media screen and (max-width:1100px) {
         flex-direction: column;
         align-items: center;
         justify-content: center;
      }
   }

   &__column {
      margin-right: 132px;

      @media screen and (max-width:1100px) {
         margin-bottom: 20px;
         margin-right: 0;

         text-align: center;
      }
   }

   &__container {
      max-width: 59.3rem;
      margin: 0 auto;

      @media screen and (max-width:1200px) {
         max-width: 49.3rem;
      }

      @media screen and (max-width:992px) {
         max-width: 39.3rem;
      }
   }

   &__column-top {
      margin-bottom: 1.93rem;

      @include textEdit(Roboto, 500, 0.625rem, 1.2);

      letter-spacing: 0.1em;
      text-transform: uppercase;

      color: $titleColor;

      @media screen and (max-width:640px) {
         margin-bottom: 0.3rem;

         text-align: center;
      }
   }

   &__column-title {
      margin-bottom: 0.875rem;

      @include textEdit(Quicksand, 500, 2.25rem, 1.25);

      color: $titleColor;


   }

   &__column-txt {
      @include textEdit(OpenSans, 600, 0.75rem, 1.9);

      letter-spacing: 0.03em;

      color: #A4ABC8;
   }

   & .swiper-container {
      width: 432px;
      padding-bottom: 60px;

      position: relative;
   }

   & .swiper-slide {
      padding: 0 0 0 0;
   }

   & .swiper-slide__txt {

      max-width: 423px;

      padding: 40px 70px 50px;

      box-sizing: border-box;

      background-color: #fff;
   }

   & .swiper-slide__img {
      position: absolute;
      bottom: -5%;
      left: 50%;
      transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      -moz-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
      -o-transform: translateX(-50%);
   }
}