.logos__list {
   max-width: 57.28rem;

   height: 9.125rem;
   margin: 0 auto;

   display: flex;
   justify-content: space-between;
   align-items: center;

   @media screen and (max-width:780px) {
      flex-wrap: wrap;

      justify-content: center;

   }
}

.logos__item {
   @media screen and (max-width:780px) {
      flex: 0 1 30%;
   }

   @media screen and (max-width:500px) {
      flex: 0 1 50%;
   }
}