.getStarted {
   padding: 91px 0;

   background: url('../assets/images/getStarted/clouds.png') no-repeat center center;
   background-color: $accentColor;





   &__top {
      margin-bottom: 1rem;

      font-family: Roboto;
      font-style: normal;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      /* identical to box height */

      letter-spacing: 0.1em;
      text-transform: uppercase;

      color: $primaryColor;


   }

   &__title {
      margin-bottom: 1.75rem;

      @include textEdit(Quicksand, 300, 3rem, 1.25);

      text-align: center;

      color: $primaryColor;

      span {
         font-weight: 500;
      }
   }

   &__txt {
      max-width: 44.3rem;

      margin: 0 auto 1.75rem auto;

      @include textEdit(Roboto, 400, 0.9rem, 1.93);

      letter-spacing: 0.03em;

      color: $primaryColor;

   }
}