.hero {
   position: relative;



   height: 570px;

   @media screen and (max-width:960px) {
      background-color: #F5F8FF;
      min-height: 100%;
   }

}

.hero__svg {
   // width: 100%;
   // height: 400px;

   object-fit: cover;

   @media screen and (max-width:1440px) {
      width: 1090px;
      height: 440px;
   }

   @media screen and (max-width:1120px) {
      width: 960px;
      height: 380px;
   }

   @media screen and (max-width:960px) {
      display: none;
   }
}

.img1,
.img2,
.img3,
.img4 {
   opacity: 0;
   transition: all .3s ease-in;
   -webkit-transition: all .3s ease-in;
   -moz-transition: all .3s ease-in;
   -ms-transition: all .3s ease-in;
   -o-transition: all .3s ease-in;

}

.group1:hover~.img1 {
   opacity: 1;
}

.group2:hover~.img2 {
   opacity: 1;
}

.group3:hover~.img3 {
   opacity: 1;
}

.group4:hover~.img4 {
   opacity: 1;
}

.container {
   position: relative;
   z-index: 2;
}

.hero__phone {
   position: absolute;

   top: 0;

   right: 0;
   left: 0;

   z-index: 1;


}


.hero__name {
   padding-top: 70px;

   @include textEdit(Roboto, 500, 0.625rem, 1.2);

   letter-spacing: 0.1em;
   text-transform: uppercase;

   color: $accentColor;
}

.hero__title {
   padding-bottom: 28px;

   @include textEdit(Quicksand, 400, 3rem, 1.25);

   color: $titleColor;
}

.hero__txt {
   max-width: 29rem;

   margin: 0 auto;

   padding-bottom: 20px;

   @include textEdit(Roboto, 400, 0.9375rem, 1.93);

   letter-spacing: 0.03em;

   color: $secondaryColor;
}


.hero__video-link {
   display: block;

   padding-bottom: 10px;

   width: 52px;
   height: 52px;

   line-height: 52px;
   margin: 0 auto;
   border-radius: 50%;
   background-color: #eaeaea;

   transition: all .3s ease-in;

   &:hover {
      background-color: #fff;
   }



}

.hero__video-img {
   transform: translate(1px, -3px);
   -webkit-transform: translate(1px, -3px);
   -moz-transform: translate(1px, -3px);
   -ms-transform: translate(1px, -3px);
   -o-transform: translate(1px, -3px);
}