.signUp {
   padding: 7.375rem 0;

   background-color: #FCFDFF;

   @media screen and (max-width:576px) {
      padding: 3rem 0;
   }

   .signUp__container {
      max-width: 53.5rem;

      padding: 0 15px;

      margin: 0 auto;
   }

   .signUp__discr {
      @media screen and (max-width:576px) {
         text-align: center;
      }
   }

   .signUp__form {
      text-align: right;

      @media screen and (max-width:576px) {
         text-align: center;
      }
   }

   .signUp__title {
      margin-bottom: 1.2rem;

      @include textEdit(Quicksand, 600, 1.125rem, 1.22);

      color: $titleColor;
   }

   .signUp__txt {
      max-width: 330px;

      @include textEdit(Roboto, 400, 0.87rem, 1.92);

      letter-spacing: 0.03em;

      color: $secondaryColor;
   }

   .signUp__input {
      display: inline;
      margin-right: 10px;

      width: 279px;
      height: 59px;
   }

   .btn {
      width: 184px;
      height: 59px;
   }
}