.footer {
   padding: 5.81rem;

   @media screen and(max-width:992px) {
      padding: 0 0 2rem;
   }

   .footer__left {
      display: flex;
      align-items: center;

      @media screen and(max-width:992px) {
         justify-content: center;
      }
   }

   .logo {
      @media screen and(max-width:992px) {
         display: none;
      }
   }

   .footer__left-list {
      display: flex;
      align-items: center;

      margin-left: 2.56rem;

      @media screen and(max-width:992px) {
         margin-left: 0;
      }


   }


   .footer__left-item {
      margin-right: 2.625rem;

      @media screen and (max-width:380px) {
         margin-right: 1.8rem;

      }



      &:last-child {
         margin-right: 0;
      }

   }


   .footer__left-link {
      font-family: "QuicksandBold";
      font-weight: bold;
      font-size: 0.75rem;
      line-height: 1.25;

      color: #697CA6;
   }

   .footer__right {
      display: flex;
      align-items: center;

      text-align: right;

      @media screen and(max-width:992px) {
         margin-top: 2rem;


         justify-content: center;

         text-align: center;


      }
   }

   .footer__right-list {
      display: flex;
   }

   .footer__right-item {
      margin-right: 2.3125rem;

      &:last-child {
         margin-right: 0;
      }

      @media screen and (max-width:380px) {
         margin-right: 1.8rem;

      }
   }

   .footer__right-btn {
      margin-left: 2.75rem;

      // width: 82px;
      // height: 17px;

      background-color: #D2E1FF;
      border-radius: 2px;

      @include textEdit(Quicksand, 600, 0.81rem, 1.23);

      text-align: center;

      color: $accentColor;
   }

   .footer__bottom {
      margin-top: 2.9rem;
   }

   .footer__bottom-txt {
      margin-left: 6.56rem;

      @include textEdit(Quicksand, 500, 0.81rem, 1.18);

      color: #AEB8D0;

      @media screen and(max-width:992px) {
         margin-left: 0;

         text-align: center;
      }
   }
}